@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono|Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.App {
  /* text-align: center; */
  flex-direction: column;
  align-items: center;
  background: #ececec;
  font-family: 'Roboto', sans-serif;
}

.app-container {
  margin: 0 7.5rem;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
    background-color: #ececec;
    display: flex;
    justify-content: space-between;
    justify-content: flex-end;
    align-items: center;
    color: black;
    padding: 0 15px;
    width: 100%;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 500;
    font-size: 1.3rem;
}

header h1 {
    text-align: center;
}

header li {
    list-style: none;
}

header a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    opacity: 0.9;
}

header a:hover {
    opacity: 1;
}

header nav {
    display: flex;
}

header nav li {
    margin: 2rem 15px;
}

header nav li:hover {
    opacity: 0.5;
}

header nav li:first-child {
    margin-left: 0;
}

header nav li:last-child {
    margin-right: 12rem;
}


.banner {
    display: flex;
    flex-direction: row;
    margin-top: 4rem;
    margin-bottom: 4rem;
    margin-left: 0;
    margin-right: 1rem;
}

.banner-pic {
    max-width: 25%;
    border-radius: 50%;
    margin-left: 4.5rem;
}

.typed-text {
    margin-top: 5rem;
    margin-left: 6rem;
}

.typewriter {
    display: flex;
    justify-content: flex-start;
    font-size: 48px;
    margin-bottom: 50px;
    letter-spacing: 5px;
}
.social-media-icons {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

.link-item {
    width: 5%;
    height: 5%;
    margin: 0 30px;
}

.link-item:hover {
    opacity: 0.6;
}

.link-item:first-child {
    margin-left: 80px;
}
.bio {
    margin: 3rem;
    text-align: center;
}

.bio h1 {
    text-align: center;
    font-family: 'IBM Plex Mono', monospace;
}
h1 {
    text-align: center;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 700;
}

.type-of-skill {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-right: 30px;
    width: 100px;
    font-size: 23px;
}

.skill-cards {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

h2.type-of-skill {
    font-size: 1.1rem;
    font-family: 'IBM Plex Mono', monospace;
}

.frontend-pic {
    width: 70%;
}

.backend-pic {
    width: 65%;
}

.tools-pic {
    width: 65%;
}

.skill-card {
    margin: 4rem 8.1rem;
}

.each-skill p {
    margin: 1.5rem 0 5px 0;
    font-family: 'Roboto', sans-serif;
}

.experience-item {
    max-width: 1200px;
    position: relative;
    display: flex;
    flex-direction: row;
    margin-bottom: 60px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.exp-content {
    z-index: inherit;
    width: 52%;
    line-height: 1.6;
    padding: 40px;
}

h3 {
    margin: 0px;
    display: block;
    text-align: left;
    margin-left: 2rem;
    font-family: 'IBM Plex Mono', monospace;
}

h4.job-position {
    display: block;
    text-align: left;
    margin-left: 2rem;
    font-style: italic;
}

h4.duration {
    display: block;
    text-align: left;
    margin-left: 2rem;
}

.job-position {
    margin: 0 0 0 2rem;
}

.description {
    text-align: left;
    margin-left: 0.5rem;
}

.right {
    display: flex;
    justify-content: flex-end;
    right: 100px;
}

img.compimg {
    width: 100%;
    align-content: right;
}
h1 {
    text-align: left;
    
}

.experience-list {
    width: 1400px;
}
.project-item {
    max-width: 1200px;
    position: relative;
    display: flex;
    flex-direction: row;
    margin-bottom: 60px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.project-content {
    z-index: inherit;
    width: 55%;
    line-height: 1.6;
    padding: 40px;
    flex-direction: column;
}

h3.name {
   margin-left: 20px;
   font-family: 'IBM Plex Mono', monospace;
   letter-spacing: 2px;
}

h4.description {
    margin-left: 20px;
    font-weight: 300;
}

p.skills {
    display: inline-block;
    margin-left: 10px;
    margin-right: 10px;
    padding: 0px 10px;
    background-color: #CAF2D7;
    border-radius: 4px;
}

p.skills:hover {
    opacity: 0.5;
}

a.demo {
    padding: 10px;
    margin-left: 10px;
    background-color: bisque;
    border-radius: 6px;
    text-decoration: none;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 5px 0 rgba(0, 0, 0, 0.19);
    color: black;
}

a.demo:hover {
    opacity: 0.5;
}

img.image {
    width: 72%;
    display: flex;
    text-align: right;
    justify-content: flex-end;
}


